// SVG tuto: https://medium.com/how-to-react/create-svg-drawing-animation-in-reactjs-using-animejs-9b124e711d5f
// https://codepen.io/NouamaneTazi/pen/oNXxWJz
// https://codesandbox.io/s/reactjs-animejs-4ilb6
// https://codesandbox.io/s/react-animejs-20buk
// https://codesandbox.io/s/react-svg-stroke-animation-ornament-x9dqo

import React from "react"
import anime from "animejs";
import { navigate } from "gatsby"


class SubmitButton extends React.Component {
    componentDidMount() {
        this.basicTimeline = anime.timeline({ autoplay: true })
    }

    handleClick = () => {
        if (!this.flag) {
            this.basicTimeline
                .add({
                    targets: ".text",
                    duration: 1,
                    opacity: "0"
                })
                .add({
                    targets: ".button",
                    duration: 1300,
                    height: 10,
                    width: 300,
                    backgroundColor: "#6e462b", //btn-color
                    border: "0",
                    borderRadius: 100
                })
                .add({
                    targets: ".progress-bar",
                    translateX: '-50%',
                    translateY: '-50%',
                    duration: 1,
                })
                .add({
                    targets: ".progress-bar",
                    duration: 2000,
                    width: 300,
                    easing: "linear"
                })
                .add({
                    targets: ".button",
                    width: 0,
                    duration: 1
                })
                .add({
                    targets: ".progress-bar",
                    width: 80,
                    height: 80,
                    delay: 500,
                    duration: 750,
                    borderRadius: 80,
                    backgroundColor: "#71DFBE" //lightblue
                })
                .add({
                    targets: ".st0",
                    duration: 1,
                    opacity: 1
                })
                .add({
                    targets: ".st0",
                    strokeDashoffset: [anime.setDashoffset, 0],
                    duration: 200,
                    easing: "easeInOutSine",
                })
                .add({
                    targets: ".st0",
                    duration: 1,
                    opacity: 0,
                    duration:750,
                    easing: "easeInCirc",
                    delay:1000,
                })
                .add({
                    targets: ".progress-bar",
                    scale: 25,
                    duration: 2000,
                    backgroundColor: 'rgb(255, 244, 223)',
                    easing: "easeInCubic"
                })
                .add({
                    complete: function(anim) {
                        navigate("/home")
                      }
                })
        }
        this.flag = true
    }
    render() {
        return (
            <div className="submit-btn" onClick={this.handleClick}>
                <div className="button">
                    <div className="text">Enter</div>
                </div>
                <div className="progress-bar"></div>
                <svg x="0px" y="0px"
                    viewBox="0 0 25 30" style={{ "enableBackground": "new 0 0 25 30" }}>
                    <path className="check st0" ref={path => (this.pathRef = path)} d="M2,19.2C5.9,23.6,9.4,28,9.4,28L23,2" />
                </svg>
            </div>
        )
    }
}

export default SubmitButton
